<template>
  <div>
    <slot></slot>
  </div>
</template>

<script type="text/ecmascript-6">
  import MeScroll from './mescroll.js'
  import totop from './mescroll-totop.png'
  import empty from './mescroll-empty.png'
  //vue
  export default {
    props: {
      upCallback: {
        type: Function,
        default: null
      },
      emptyDataBtnClick: {
        type: Function,
        default: null
      },
      toTopClass: {
        type: String,
        default: 'mescroll-totop'
      },
      upAuto: {
        type: Boolean,
        default: true
      },
      warpId: {
        type: String,
        default: 'mescroll'
      },
      upUse: {
        type: Boolean,
        default: true
      },
      scrollId: {
        type: String,
        default: 'body'
      },
      scrollTop: {
        type: Number,
        default: 0
      },
    },
    data() {
      return {
        mescroll: null,
        scrollIds:{},
      }
    },
    mounted() {
      //Mescroll,down,,
      //: mescroll.resetUpScroll(); resetUpScrollpage.num=1,up.callback,;
      let self = this;
      this.mescroll = new MeScroll(self.scrollId, {
        down: {use: false},
        up: {
          use:self.upUse,
          auto: self.upAuto,
          callback: self.upCallback, //
          page: {size: 10}, //8,10
          toTop: { //
            src: totop, //1000px,offset
            offset: 1000,
            warpClass: self.toTopClass
          },
          noMoreSize: 5,  //,;(),; 5
          empty: { //
            warpId: self.warpId,
            icon: empty,
            tip: "~",
            btntext: "~",
            btnClick: self.emptyDataBtnClick || self.btnClick,
          },
          warpId: self.warpId,
          htmlNodata: '<p class="upwarp-nodata">~</p>',
          scrollbar: {use: true, barClass: "mescroll-bar"},
          htmlLoading: '<p class="upwarp-progress mescroll-rotate"></p><p class="upwarp-tip">..</p>',
        }
      });
    },
    methods: {
      // page = {num:1, size:10}; num: ,1; size:,10
      endSuccess(curPageDataLength){
        this.mescroll && this.mescroll.endSuccess(curPageDataLength);
      },
      // ():  totalSize
      endSuccess(curPageDataLength, totalSize){
        this.mescroll && this.mescroll.endSuccess(curPageDataLength, totalSize);
      },
      endErr(){
        this.mescroll && this.mescroll.endErr();
      },
      deactivated(){
        this.mescroll && this.mescroll.deactivated();
      },
      activated(){
        if (!(this.mescroll && this.mescroll.beActivated)) {
          this.mescroll.activated();
        }
      },
      getScrollTop(){
        if(this.mescroll){
          return this.mescroll.getScrollTop();
        }
        return 0;
      },
      scrollTo(top,duration){
        this.mescroll && this.mescroll.scrollTo(top,duration);
      },
      btnClick() {
        alert(",");
      },
      resetUpScroll(){
        this.mescroll && this.mescroll.resetUpScroll();
      }
    },
    computed: {
      list() {
        return this.$store.state.base_data.list
      }
    },
    destroyed(){
      this.mescroll &&this.mescroll.destroy();
    },
    deactivated(){
      this.deactivated();
      let warpId = this.warpId;
      let scrollMap = {}
      scrollMap[warpId] = this.mescroll.preScrollY;
      this.$store.dispatch('setScrollTop', scrollMap)
    },
    activated(){
      this.activated();
      let scrollTop = 0;
      if(this.$store.state.scrollTops.hasOwnProperty(this.warpId)){
        scrollTop = this.$store.state.scrollTops[this.warpId];
      }
      this.scrollTo(scrollTop,0)
    }
  };
</script>

<style lang="less" >
  @import './mescroll.css';
</style>



